* {
  margin: 0;
  padding: 0;
}
:root {
  --color-accent: #FF7B92;
  --color-background:rgb(27, 27, 27);
}
@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap');
body{
  font-family: 'Source Sans Pro', sans-serif;
  margin:auto 0;
  background-color: var(--color-background);
  color: white;
}
input,textarea, label, button{
  font-family: 'Source Sans Pro',sans-serif;
  outline:none;
  background: 0;
  cursor: text;
  border: 0;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
button{
  background-color: var(--color-accent);
  color: white;
  width: 100%;
  height: 44px;
  font-size: 16px;
  border-radius: 12px;
  cursor: pointer;
}
.center{
  display: flex;
  align-items: center;
  justify-content: center;
}
.main_wrapper{
  gap: 20px;
  height: 100vh;
  width: 100vw;
  max-width: 300px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
img{
  border-radius: 50%;
}
.l{
  margin-left: 14px;
  font-size: 16px;
}
.test{
    font-size: 12px;
      margin-left: 14px;
color: rgba(255, 255, 255, 0.6);
}
.lol{
  margin: 12px 0px 0px;
  font-weight: 400;
font-size: 14px;
}