.logo_inner {
  width: 170px;
  height: 170px;
  background-color: #2B2B2B;
  border-radius: 50%;
}
.auth_form{
  width: 100%;
  position: relative;
}
.phone_number,.password_input {
  border: 1px rgba(255, 255, 255, 0.507) solid;
  border-radius: 12px;
  height: 44px;
  width: 97%;
  font-size: 16px;
  padding-left: 8px;
  color: white;
  transition: 0.3s all;
}
.label_phone_number,.label_password {
  display: block;
  position: absolute;
  background-color: var(--color-background);
  font-size: 16px;
  opacity: 0.5;
  transition: 0.3s all;
}
.label_phone_number{
  left: 12px;
  top: 13px;
}
.password_input{
  margin-top: 15px;
  display: none;
}
.label_password{
  left: 12px;
  bottom: 13.5px;
  display:none;
}
.phone_number:focus + .label_phone_number {
  transform: scale(0.75) translate(-1.5rem, -2rem);
  color: var(--color-accent);
  opacity: 1;
}
.password_input:focus + .label_password {
  transform: scale(0.75) translate(-1rem, -2rem);
  color: var(--color-accent);
  opacity: 1;
}
.phone_number:focus {
  border: 1px var(--color-accent) solid;
}
.password_input:focus {
  border: 1px var(--color-accent) solid;
}